@import "@coreui/coreui/scss/functions";
@import "@coreui/coreui/scss/variables";
@import "@coreui/coreui/scss/mixins";

.wrapper {
    width: 100%;
    @include ltr-rtl("padding-left", var(--cui-sidebar-occupy-start, 0));
    will-change: auto;
    @include transition(padding .15s);
}
.simplebar-content {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}
.example {
    &:not(:first-child) {
        margin-top: 1.5rem;
    }

    .tab-content {
        background-color: $light-50 !important;

        @at-root .dark-theme & {
            background-color: rgba(255, 255, 255, .1) !important;
        }
    }

    & + p {
        margin-top: 1.5rem;
    }

    // Components examples
    .preview {
        + p {
            margin-top: 2rem;
        }

        > .form-control {
            + .form-control {
                margin-top: .5rem;
            }
        }

        > .nav + .nav,
        > .alert + .alert,
        > .navbar + .navbar,
        > .progress + .progress {
            margin-top: 1rem;
        }

        > .dropdown-menu {
            position: static;
            display: block;
        }

        > :last-child {
            margin-bottom: 0;
        }

        // Images
        > svg + svg,
        > img + img {
            margin-left: .5rem;
        }

        // Buttons
        > .btn,
        > .btn-group {
            margin: .25rem .125rem;
        }
        > .btn-toolbar + .btn-toolbar {
            margin-top: .5rem;
        }

        // List groups
        > .list-group {
            max-width: 400px;
        }

        > [class*="list-group-horizontal"] {
            max-width: 100%;
        }

        // Navbars
        .fixed-top,
        .sticky-top {
            position: static;
            margin: -1rem -1rem 1rem;
        }

        .fixed-bottom {
            position: static;
            margin: 1rem -1rem -1rem;
        }

        @include media-breakpoint-up(sm) {
            .fixed-top,
            .sticky-top {
                margin: -1.5rem -1.5rem 1rem;
            }
            .fixed-bottom {
                margin: 1rem -1.5rem -1.5rem;
            }
        }

        // Pagination
        .pagination {
            margin-top: .5rem;
            margin-bottom: .5rem;
        }
    }
}
